<template>
  <sidebar-menu :menu="menu" @toggle-collapse="onToggleCollapse"/>
</template>
<script>
export default {
  data() {
    return {
      collapse:false,
      menu: [
        {
          header: true,
          title: "EASYNOTICE",
          hiddenOnCollapse: false
        },
        {
          href: "/dashboard/board",
          title: "Board",
          icon: "fa fa-user"
        },
        {
          href: "/dashboard/update",
          title: "Update Content",
          icon: "fa fa-user"
        },
        {
          href: "/dashboard/layout",
          title: "Change Layout",
          icon: "fa fa-user"
        },
        {
          href: "/dashboard/assets",
          title: "Upload assets",
          icon: "fa fa-user"
        },
        {
          href: "/dashboard/groups",
          title: "Groups",
          icon: "fa fa-user"
        }
      ]
    };
  },
  methods:{
  onToggleCollapse(item){
    this.collapse = item;
    this.menu[0].title = this.collapse?"EN":"EASYNOTICE";
    this.$emit("toggle-collapse",item)
  }
  }
};
</script>
